<template>

<div>
<!--   id"  lab
fid"  la
uri"  la
route"
authRole -->
  <el-row><!--   -->
    <el-col :span="10"><el-input placeholder="" v-model="input" class="input-with-select">
      <el-button slot="append" icon="el-icon-search"></el-button>
    </el-input>
    </el-col>
  <el-col :span="5">
    <el-button @click="reload"></el-button>
  </el-col>
    <el-col :span="5">
      <el-button @click="reload"></el-button>
    </el-col>
    <!---->
    <auth-search></auth-search></el-row>

<!--  -->
  <el-table :data="datas">
    <el-table-column  prop="id"  label="ID"  width="180"></el-table-column>
    <el-table-column  prop="serviceName"  label=""></el-table-column>
    <el-table-column  prop="uri"  label="URI"></el-table-column>
    <el-table-column  prop="route"  label=""></el-table-column>
    <el-table-column   label="">
      <template slot-scope="scope">
        <el-tag v-for="item in scope.row.authRole" :key="item.id" type="success">{{item}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column  prop="operUser"  label="UserAccount"></el-table-column>
    <el-table-column   label="">
      <template slot-scope="scope">
        <el-tag v-if="scope.row.checkLogin==true" type="success"></el-tag>
        <el-tag v-else type="danger"></el-tag>
      </template>
    </el-table-column>
    <el-table-column    label="">
      <template slot-scope="scope">
        <el-tag v-if="scope.row.isDefault==true" type="success"></el-tag>
        <el-tag v-else type="danger"></el-tag>
      </template>
    </el-table-column>
    <el-table-column   label="">
      <template slot-scope="scope">
        <el-tag v-if="scope.row.isOpen==true" type="success"></el-tag>
        <el-tag v-else type="danger"></el-tag>
      </template>
    </el-table-column>
    <!--      -->
    <el-table-column  label="">
      <template slot-scope="scope">
<!--        -->
        <el-button type="primary" size="mini" @click="openAuth(scope.row)" v-if="!scope.row.isOpen"></el-button>
<!--        -->
        <el-button type="warning" size="mini" @click="closeAuth(scope.row)" v-else></el-button>
<!--        -->
        <el-button type="danger" size="mini" @click="delAuth(scope.row)"></el-button>
        </template>
    </el-table-column>
  </el-table>
</div>
</template>

<script>
import authSearch from "@/view/auth-admin/auth-search";
import {AuthApi} from "@/core/request/api/AuthApi";

export default {
  name: "authPage",
  components: {authSearch},
  data(){
    return{
      datas:null
    }
  },
  methods:{
    reload(){
      this.axios.get(AuthApi.reload).
      then(res=>{
        if (!res.data.code){
          this.$message.success("")
        }
      })
    },
    openAuth(data) {
      this.axios.put(AuthApi.open, {id: data.id}).then(
          res => {
            if (!res.data.code) {
              data.isOpen = true
              this.$message.success("")
            }
          }
      )
    },
    closeAuth(data){
      this.axios.put(AuthApi.close,{id:data.id}).then(res=>{
        if(!res.data.code){
          data.isOpen=false
          this.$message.success("")
        }
      })
      },
    delAuth(data){
      this.axios.delete(AuthApi.del,{id:data.id})
          .then(res=>{
            if(!res.data.code){
              data=null
              this.$message.success("")
            }
          })
    }

  },
  mounted() {
    this.axios.get(AuthApi.list,{
      params:{
        current:1,
        pageSize:10
      }
    }).then(res =>{
      this.datas=res.data.data.records
    })
  }
}
</script>

<style scoped>

</style>